  --clr-ntrl-max: #000;
  --font-size-base: 2rem;
  --shadow-primary: 0 0 2rem .2rem rgba(15, 165, 229, .4);
  --array-bar-width: 5px;
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
}
@media only screen and (max-width: 62.5em) {
  html {
    font-size: 50%;
  }
}
@media only screen and (max-width: 50em) {
  html {
    font-size: 45%;
  }
}

ul {
  list-style: none;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: var(--font-size-base);
  color: var(--clr-ntrl-min);
  background-color: var(--clr-base);
}
body::-webkit-scrollbar {
  display: none;
}

.navbar {
  position: relative;
  padding: 2.5rem 0;
}
@media only screen and (max-width: 62.5em) {
  .navbar {
    padding: 2.5rem;
  }
}
.navbar__list {
  display: flex;
  justify-content: center;
}
@media only screen and (max-width: 62.5em) {
  .navbar__list {
    justify-content: space-between;
  }
}
.navbar__item {
  flex: 1;
  align-items: center;
  display: flex;
  justify-content: center;
}
@media only screen and (max-width: 37.5em) {
  .navbar__item--generate, .navbar__item--sort {
    display: none;
  }
}
@media only screen and (max-width: 62.5em) {
  .navbar__item {
    flex: initial;
  }
}

.array {
  margin: 10rem auto;
  width: 80%;
}
@media only screen and (max-width: 62.5em) {
  .array {
    width: 90%;
  }
}
.array__info {
  display: flex;
  justify-content: space-between;
  font-size: 3rem;
}
.array__info span {
  margin-left: 1rem;
}
@media only screen and (max-width: 50em) {
  .array__info {
    font-size: 2.5rem;
  }
}
.array__bars {
  height: 500px;
  display: flex;
  align-items: end;
  margin-top: 5rem;
}
@media only screen and (max-width: 50em) {
  .array__bars {
    height: 250px;
  }
}
.array__bar {
  flex: 1;
  background-image: linear-gradient(180deg, var(--clr-primary-lt), var(--clr-primary), var(--clr-base-lt));
}
.array__bar:not(:last-child) {
  margin-right: 0.4rem;
}
@media only screen and (max-width: 87.5em) {
  .array__bar:not(:last-child) {
    margin-right: 0.2rem;
  }
}
.array__bar--current {
  box-shadow: 0 0 1rem 0.8rem rgb(15, 165, 229);
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: var(--clr-base);
  opacity: 0.75;
  transition: all 0.2s;
  z-index: 10;
}
.overlay--hidden {
  visibility: hidden;
  opacity: 0;
}

.btn {
  outline: none;
  color: inherit;
  font-size: inherit;
  border: 0.2rem solid var(--clr-primary);
  padding: 1rem 2.5rem;
  border-radius: 10rem;
  box-shadow: var(--shadow-primary);
  transition: background-color 0.2s;
  cursor: pointer;
}
.btn--generate {
  background-color: var(--clr-primary);
}
.btn--generate:active {
  background-color: var(--clr-primary-lt);
}
@media (hover: hover) {
  .btn--generate:hover {
    background-color: var(--clr-primary-lt);
  }
}
.btn--sort {
  background-color: transparent;
}
.btn--sort:active {
  background-color: var(--clr-primary);
}
@media (hover: hover) {
  .btn--sort:hover {
    background-color: var(--clr-primary);
  }
}
.btn--drop-down {
  border: none;
  box-shadow: none;
}
.btn--drop-down:hover {
  background-color: rgba(250, 250, 250, 0.1);
}

.btn-container {
  position: relative;
  display: none;
  padding: 2rem 0;
  margin-bottom: 10rem;
}
@media only screen and (max-width: 37.5em) {
  .btn-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 2.5rem;
  }
}

.drop-down {
  position: relative;
}
.drop-down__list {
  position: absolute;
  text-align: center;
  display: flex;
  flex-direction: column;
  min-width: 20rem;
  background-color: var(--clr-base-lt);
  left: 50%;
  top: 110%;
  translate: -50% 0;
  border-radius: 1.5rem;
  overflow: hidden;
  visibility: hidden;
  opacity: 0;
  transition: all 0.5s;
}
.drop-down__item {
  padding: 1.5rem 2.5rem;
  background-image: linear-gradient(90deg, var(--clr-base-lt), var(--clr-primary), var(--clr-primary-lt));
