}
.drop-down__item:hover {
  background-size: 100% 0.5rem;
  background-color: var(--clr-base-ltr);
}
.drop-down__icon {
  display: inline-block;
  width: 1.2rem;
  height: 1.2rem;
  border: solid var(--clr-ntrl-min);
  border-width: 0 0.25rem 0.25rem 0;
