    display: none;
  }
}
.drop-down:hover .drop-down__icon {
  rotate: -135deg;
  margin-bottom: 0;
  margin-top: 0.5rem;
}
.drop-down:hover .drop-down__list {
  visibility: visible;
  opacity: 1;
}

.size__label {
  margin-right: 2rem;
}
@media only screen and (max-width: 50em) {
  .size__label {
    margin-right: 1rem;
  }
}
.size__range {
  outline: none;
  width: 50%;
  cursor: pointer;
